@use 'sass:math';
@use 'functions' as *;

@mixin fluid-text($max: 48, $min: 16) {
    font-size: fluid($max, $min)
}

@mixin abs-center {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
}

@mixin square($size) {
    width: $size;
    height: $size;
}

@mixin visually-hidden {
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    margin: -1px !important;
    border: 0 !important;
    padding: 0 !important;
    white-space: nowrap !important;
    clip-path: inset(100%) !important;
    clip: rect(0 0 0 0) !important;
    overflow: hidden !important;
}

@mixin d-flex($justify-content: start, $aling-items: stretch, $flex-direction: row, $flex-wrap: nowrap, $column-gap: 0, $row-gap: 0) {
    display: flex;
    justify-content: $justify-content;
    align-items: $aling-items;
    flex-direction: $flex-direction;
    flex-wrap: $flex-wrap;
    column-gap: $column-gap;
    row-gap: $row-gap;
}